.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 40%;
		z-index: 3;
		transform: translate(-35%, -15%) rotate(20deg);
		@include max-lg {
			width: 50%;
		}
		@include max-md {
			width: 60%;
		}
		img {
			transform-origin: 30% 20%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 60%;
			transform: translate(-20%, 20%) rotate(-35deg);
			img {
				transform-origin: 70% 40%;
			}
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 40%;
		transform: translate(40%, 0) rotate(-15deg) scaleX(-1);
		z-index: 3;
		@include max-lg {
			width: 60%;
		}
		@include max-md {
			width: 70%;
		}
		img {
			transform-origin: 30% 20%;
		}
		.orn-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 50%;
			transform: translate(-40%, 0) rotate(-20deg);
			img {
				transform-origin: 70% 40%;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 80%;
			@include max-md {
				width: 100%;
			}
			.cover-body {
				position: relative;
				width: 100%;
				min-height: 80%;
				padding: 3rem 2rem;
				display: flex;
				justify-content: center;
				align-items: center;
				@include max-md {
					padding: 2rem 1rem;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					&::after {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						background: rgba($color: #fff, $alpha: 0.8);
						content: "";
						border-radius: var(--border-radius);
						box-shadow: inset 0 10px 20px rgba($color: #000000, $alpha: 0.15);
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						position: relative;
						border-radius: var(--border-radius);
					}
				}
				.cover-description {
					position: relative;
					padding: 0 2rem;
					text-align: center;
					z-index: 10;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
